import { ReactNode } from 'react';
import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useCircle from '../../../hooks/circle/useCircle';
import { CircleDefinition } from '../../../provider/connections/CircleDataTypes';
import Arrow from '../../Icons/Arrow/Arrow';
import Check from '../../Icons/Check/Check';
import Circles from '../../Icons/Circles/Circles';

const CirclePermissionView = ({
  circleDef,
  permissionDetails,
  className,
  isChecked,
  onClick,
}: {
  circleDef: CircleDefinition;
  permissionDetails?: string;
  className?: string;
  isChecked?: boolean;
  onClick?: () => void;
}) => {
  const { data: members } = useCircle({ circleId: circleDef?.id }).fetchMembers;

  if (!circleDef) {
    return <></>;
  }

  const LinkWrapper = ({ children }: { children: ReactNode }) =>
    onClick ? (
      <>{children}</>
    ) : (
      <Link
        to={`/owner/circles/${encodeURIComponent(circleDef.id)}`}
        className="contents hover:text-slate-700 hover:underline dark:hover:text-slate-400"
      >
        {children}
      </Link>
    );

  const checkedClasses = isChecked
    ? 'border-indigo-500 bg-slate-50 dark:border-indigo-700 dark:bg-slate-700'
    : isChecked === false
    ? 'border-slate-100'
    : '';

  return (
    <div
      className={`flex flex-row ${className ?? ''} ${circleDef.disabled && 'opacity-50'} ${
        onClick && 'cursor-pointer'
      } ${checkedClasses}`}
      onClick={() => onClick && onClick()}
    >
      <LinkWrapper>
        {isChecked ? (
          <Check className="mt-1 mb-auto mr-3 h-6 w-6" />
        ) : (
          <Circles className="mt-1 mb-auto mr-3 h-6 w-6" />
        )}

        <div className="mr-2 flex flex-col">
          <p className={`my-auto leading-none`}>
            {circleDef.disabled && t('Disabled:')} {circleDef?.name}
            {permissionDetails && `: ${permissionDetails}`}
          </p>
          <small className="block">
            {members
              ? `${members.length} ${members.length === 1 ? t('member') : t('members')} ${t(
                  'receives'
                )} `
              : null}
            {circleDef?.driveGrants
              ? `${t('access on')} ${circleDef.driveGrants.length} ${
                  circleDef.driveGrants.length >= 2 ? t('drives') : t('drive')
                }`
              : null}
          </small>
        </div>
        {!onClick && <Arrow className="my-auto ml-2 h-5 w-5" />}
      </LinkWrapper>
    </div>
  );
};

export default CirclePermissionView;
